import { Component, ViewChild } from '@angular/core';
import {  AlertController,ViewController,IonicPage,  NavController,Events , ActionSheetController, NavParams } from 'ionic-angular';
import { FormControl,  FormBuilder,  FormGroup,   Validators } from '@angular/forms';
import { Auth,NotificationProvider } from '../../../providers';
import { TranslateService } from '@ngx-translate/core';
import { Storage } from '@ionic/storage';
import { IonicSelectableComponent } from 'ionic-selectable';
import { Form } from '../../../types';


@IonicPage()
@Component({
  selector: 'page-cursoslibres-create',
  templateUrl: 'cursoslibres-create.html',
})
export class CursoslibresCreatePage {
  @ViewChild('fileInput') fileInput;
  @ViewChild('portComponent') portComponent: IonicSelectableComponent;
  @ViewChild('portComponent2') portComponent2: IonicSelectableComponent;
  public categoria		     : any;
  public vcategoria  :any=[];
  public form                  : FormGroup;
  public plataforma                 : any;
  public nombre		     : any;
  public url		     : any;
  public idevento		     : any;
  public catedratico		     : any;
  public email		     : any;
  public capacidad		     : any;
  public estado		     : any;
  public clave		     : any;
  public telefono		     : any;
  public fecha		     : any;
  private _ID                  : String;
  idempresa:any;
  public image              	: any;
  public pageTitle           	: string;
  username: string = "";

  isDisabledplataforma :any;
  constructor( public alertCtrl: AlertController,public storage: Storage, public msj: NotificationProvider, public translateService: TranslateService,public viewCtrl: ViewController,public authService:  Auth,public navCtrl 		: NavController,
    public navParams 	: NavParams,   private _FB          : FormBuilder,         public actionSheetCtrl: ActionSheetController,
          public events: Events)
  {
    this.translateService.get('APPX').subscribe((value) => {
      this.idempresa=value
    });
     this.form = this._FB.group({
        plataforma        	   :  ['', Validators.required],
        nombre     : ['', Validators.required],
        url     : ['', Validators.required],
        fecha     : ['', Validators.required],
        capacidad     : ['', Validators.required],
        estado     : ['', Validators.required],
        idevento     : ['', Validators.required],
        categoria     : ['', Validators.required],
        clave     : ['', Validators.required],
        catedratico      : ['', Validators.required],
        email       : ['', Validators.compose([Validators.maxLength(50), Validators.pattern(/^(([^<>()\[\]\\.,;:\s@"]+(\.[^<>()\[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/), Validators.required])],
        telefono       : ['', Validators.compose([Validators.maxLength(8), Validators.pattern(/^\d{8}$/), Validators.required])],

     });

     this.authService.getReg('todos/categoria-cursoslibres/' + this.idempresa,'frmcats').then((data) => {
      this.vcategoria=data;
     });

  }

  ngOnInit() {
    this.storage.get('username').then((value) => {
      this.username = value;
  });


  this.portNameControl2 = this._FB.control(null, Validators.required);

  this.portForm2 = this._FB.group({
    portName: this.portNameControl2

  });
  }

  ionViewDidLoad() : void
  {
    if(this.navParams.get("record")==null)
    {

      this.translateService.get('ITEM_CREATE_TITLE').subscribe((value) => {
        this.pageTitle=value;
      });

      this.isDisabledplataforma =true
    }
    else {
      //this._ID 				=	this.navParams.data.record.plataforma;
       this._ID 				=	this.navParams.data.record._id;
       this.plataforma 				=	this.navParams.data.record.plataforma;
       this.nombre		=	this.navParams.data.record.nombre;
       this.url			=	this.navParams.data.record.url;
       this.catedratico  			=	this.navParams.data.record.catedratico;
       this.email			=	this.navParams.data.record.email;
       this.fecha			=	this.navParams.data.record.fecha;
       this.telefono		=	this.navParams.data.record.movil;
       this.capacidad			=	this.navParams.data.record.capacidad;
       this.clave			=	this.navParams.data.record.clave;
       this.estado			=	this.navParams.data.record.estado;
       this.categoria		=	this.navParams.data.record.idarea;
       this.idevento		=	this.navParams.data.record.idevento;
       this.isDisabledplataforma =false
       this.translateService.get('ITEM_UPDATE_TITLE').subscribe((value) => {
        this.pageTitle=value;
      });


    }
  }



  manageGallery() : void
  {
    let creat='Actualiza';
    if(this.navParams.get("record")==null)
    {  creat='Crea';
    }

     let   options       : any	     = {
       idempresa:this.idempresa,
       idtipoevento   :'Cursos libres en linea',
     plataforma : this.form.controls['plataforma'].value,
     nombre : this.form.controls['nombre'].value,
     url : this.form.controls['url'].value,
     catedratico : this.form.controls['catedratico'].value,
     email : this.form.controls['email'].value,
     fecha : this.form.controls['fecha'].value,
     movil : this.form.controls['telefono'].value,
     capacidad : this.form.controls['capacidad'].value,
     clave : this.form.controls['clave'].value,
     idevento : this.form.controls['idevento'].value,
     estado : this.form.controls['estado'].value,
     idarea : this.form.controls['categoria'].value,
     bitacora:{idempresa:this.idempresa,idafiliado:'',email: this.username,permiso:creat,accion:creat+' area_eventos '}
    };


     if(this.navParams.get("record")==null)
     {
          if(options){
            this.authService.createReg('',options,'area_eventos').then((result) => {
              if (!this.form.valid) { return; }
              this.viewCtrl.dismiss(result);
          }, (err) => {
            this.msj.showError3('Error',err.error);
          });
          }
     }
     else
     {
      if(options){
        this.authService.createReg(this._ID,options,'area_eventos').then((result) => {
           if (!this.form.valid) { return; }
           this.viewCtrl.dismiss(result);
       }, (err) => {
        this.msj.showError3('Error',err.error);
       });
    }}
  }

  cancel() {
    this.viewCtrl.dismiss();
  }
  done() {
    if (!this.form.valid) { return; }
    //this.viewCtrl.dismiss(this.form.value);
  }



//********************************************************************************************* */


//********************************************************************************************* */
port2: Form;
portForm2: FormGroup;
portNameControl2: FormControl;
  onAddForm2(event: {
    component: IonicSelectableComponent
  }) {
    this.portNameControl2.reset();
    this.portNameControl2.setValue(event.component.searchText);
    event.component.showAddItemTemplate();
  }

  onSaveForm2(event: {
    component: IonicSelectableComponent,
    item: Form
  }) {
    this.portNameControl2.setValue(event.item.nombre);
    event.component.showAddItemTemplate();
  }




  deleteSelectedItem2(op) {
    let itemToDeleteIndex;

    this.vcategoria.forEach((selectedItem, itemIndex) => {
      if (selectedItem._id==op) {
        itemToDeleteIndex = itemIndex;
      }
    });

    this.vcategoria.splice(itemToDeleteIndex, 1);
  }

  onDeleteForm2(event: {
    component: IonicSelectableComponent,
    item: Form
  },op2) {
    let alert = this.alertCtrl.create({ title: 'Seguro de realizar esta operaciÃ³n?',
    buttons: [
      {text: 'Si',
        handler: () => {
          var aa:any;
          aa=event.item
          let recordID 		: string		= aa.id;
          this.authService.deleteReg(recordID+'/'+this.username+ '/' + this.idempresa + '/' +'','frmcats').then((data) => {
            event.component.deleteItem({id:recordID,nombre:'aaa'});
            this.deleteSelectedItem2(recordID);

          }, (err) => {
            this.msj.showError3('Error',err.error);
          });

        }
      },
      {
        text: 'No',  handler: () => { }
      }
    ]
  });
  alert.present();
  }

  addForm2(op) {
    let creat='Crea';
     let   options       : any	     = {
       idempresa: this.idempresa,
     idunidad : op,
     nombre : this.portNameControl2.value,
     bitacora:{idempresa:this.idempresa,idafiliado:'',email: this.username,permiso:creat,accion:creat+' plantillas '}
    };
    if(options){
      this.authService.createReg('',options,'frmcats').then((result) => {

        var aa:any=result;
        let port = new Form({
          id: aa._id,
          nombre: this.portNameControl2.value

        });
        this.portComponent2.addItem(port).then(() => {
          this.portComponent2.search(port.nombre);
        });
        this.portNameControl2.reset();
        this.portComponent2.hideAddItemTemplate();
    }, (err) => {
      this.msj.showError3('Error',err.error);
    });
    }



  }

  saveForm2(port: Form,op) {
    let creat='Actualiza';
    let   options       : any	     = {
      idempresa:this.idempresa,
    idunidad : op,
    nombre : this.portNameControl2.value,
    bitacora:{idempresa:this.idempresa,idafiliado:'',email: this.username,permiso:creat,accion:creat+' plantillas'}
   };
   if(options){
     var aa:any;
     aa=port;
     this.authService.createReg(aa._id,options,'frmcats').then((result) => {
       port.nombre = this.portNameControl2.value;
       this.portComponent2.hideAddItemTemplate();
   }, (err) => {
     this.msj.showError3('Error',err.error);
   });
  }
}
}



